<template>
    <section class="MbLayout">
         <div class="MbLayout__B1">
            <div class="MbLayout__B1--BxCard" 
                :class="{BxActive: sbar == 'open'}" 
                v-for="(n, i) in coachInfo.data" :key="i">

                <div class="CardBox" 
                    :class="{CbBactive: sbar == 'open'}">
                    <div class="CardBox__Top" 
                        :class="{CdTopactive: sbar == 'open'}" 
                        v-bind:style="{ 'background-image': 'url(' + n.picture.original + ')' }">
                    </div>
                    <div class="CardBox__Bottom">
                        <div class="CardBox__Bottom--Name">
                            <p class="Names">{{n.name}}</p>
                            <!-- <p class="Captions">Explore popular tourist destinations as well as hidden local favourites.</p> -->
                        </div>
                        <div class="CardBox__Bottom--InfoDetail">
                            <div class="IDlTop">
                                <div class="IDlTop__Dt1">
                                    <div class="IDlTop__Dt1--token">
                                        <span>
                                            {{n.fee}}
                                        </span> tokens*
                                    </div>
                                    <div class="IDlTop__Dt1--Ratings">
                                        <div class="Rate">
                                            <Rating :value="n.rating" />
                                        </div>
                                        <div class="Review">28 reviews</div>
                                    </div>
                                </div>
                                <button class="BtnBook" @click="BookNow(n.id)">Book now</button>
                            </div>
                            <div class="IDlBotom mt-3 text-gray-600 text-sm md:text-base">
                                <div class="IDlBotom__in1">
                                    <map-pin-icon size="1x"></map-pin-icon>
                                    <span>London, UK</span>
                                </div>
                                <div class="IDlBotom__in1">
                                    <volume-2-icon size="1.3x"></volume-2-icon>
                                    <span>London, UK</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
    
       

   
</template>


<script>
    import Rating from '@/components/utilities/Rating.vue'
    import { AllFilter } from '../../mixin/filter'
    import { mapState } from 'vuex';
    import { MapPinIcon, Volume2Icon } from 'vue-feather-icons'
    export default {
        components: {
            MapPinIcon,
            Volume2Icon,
            Rating
        },
        mixins: [
            AllFilter
        ],
        mounted() {
            this.$store.dispatch('booking/getallcoach');
        },
        methods: {
            BookNow (id) {
                // let coachID = this.coachInfo.indexOf(n)
                
                this.$store.commit('booking/mutcoachID', id)
                
                this.$router.replace(
                    { 
                        name: "coachschedule", 
                        params: {
                            id:id
                        }
                    }
                )
                
            }
        },
        computed: {
            ...mapState({
                coachInfo: state => state.booking.allcoach,
                cocahID: state => state.booking.coachID
            }),

            sbar(){
                return this.$store.state.booking.sBar
            }
        }
    }
</script>