<template>
    <div class="rating-holder">
        <div class="live-rating" :data-rating-value="nearestValue">
            <button>1</button>
            <button>2</button>
            <button>3</button>
            <button>4</button>
            <button>5</button>
        </div>
    </div>
</template>

<script>
export default {
    props: ['value'],
    computed: {
        nearestValue() {
            return Math.ceil(this.value / 0.25) * 0.25;
        }
    },
}
</script>