<template>
    <section class="MainBooking">
        <div class="MainBooking__Header">
            <HeadNav />
        </div>
        <div 
            class="MainBooking__CardBooking" id="CD1">
            <CoachCard />
        </div>
    </section>
</template>

<script>
    import CoachCard from '@/components/booking/CoachCard';
    import HeadNav from '@/components/booking/TabNav';
    export default {
        components: {
            CoachCard,
            HeadNav
        },
        data: () => ({
            windowWidth: 0,
            windowHeight: 0,
            padding: null
        }),
        mounted() {
            this.$store.dispatch('booking/getallcoach');
            this.$nextTick(function() {
                window.addEventListener('resize', this.getWindowWidth);
                window.addEventListener('resize', this.getWindowHeight);

                //Init
                this.getWindowWidth()
                this.getWindowHeight()
            })
        },
         methods: {
            getWindowWidth() {
                this.windowWidth = document.documentElement.clientWidth;
            },

            getWindowHeight() {
                this.windowHeight = document.documentElement.clientHeight;
            }
        },
        beforeDestroy() {
            window.removeEventListener('resize', this.getWindowWidth);
            window.removeEventListener('resize', this.getWindowHeight);
        }
    }
</script>